      zip: 200333
    }
  ];

  private handleClick(rows: [{}], type: string, index: number) {
    if (type === 'del') {
      this.$confirm('你确定要删除该行数据吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    } else if (type === 'edit') {
      this.$message({
        type: 'info',
        message: '编辑'
      });
      console.log('edit ', rows);
    } else if (type === 'detail') {
      this.$message({
        type: 'info',
        message: '查看详情'
      });
      console.log('detail ', rows);
    }
  }
}



